#header {
  width: var(--side-bar-width);
  height: 100%;
  padding: 4em 4em;
  position: fixed;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-between;
  text-align: left;
  background-color: #333;
  font-size: var(--main-font-size);
}

#header * {
  color: rgba(255, 255, 255, 0.5);
}

#header *:hover {
  color: #f5f5f5;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}

#header .blog-avatar {
  margin-bottom: 1em;
  width: 16em;
  height: 16em;
  background-repeat: no-repeat;
  background-size: auto 100%;
  background-position: center;
  border-radius: 100%;
}

#header > .inner {
  margin: 0 0 0 0;
}

#header .blog-title {
  font-size: 2.2em;
  line-height: 2em;
  color: #f5f5f5;
}

#header .blog-subtitle {
  font-size: 1.8em;
  line-height: 2em;
  color: #2f6ce6;
}

#header .blog-title,
#header .blog-subtitle,
#header .blog-desc {
  font-weight: 400;
}

#header .blog-desc {
  font-size: 1em;
  line-height: 1.5em;
}

.block-blank {
  height: 20em;
}

#footer .links {
  margin-bottom: 2em;
}

#footer .links li {
  margin: 0;
  list-style: none;
}

#footer .links .link {
  font-size: 2em;
  line-height: 2em;
}

#footer .copyright {
  font-size: 0.6em;
  line-height: 1em;
  margin: 0;
}

#posts {
  padding: 6em 4em 2em 4em;
  overflow: hidden;
}

#posts .list-title {
  font-size: 1.4em;
  line-height: 2.2em;
  margin: 1.6em 0 1em 0;
}

#posts .row {
  margin: 2em 0 -1px -2.5em;
  border-bottom: solid 1px transparent;
  box-sizing: border-box;
}

/* #posts .row > * {
  float: left;
  box-sizing: border-box;
  padding: 0 0 0 2.5em;
} */

#posts .row:after,
#posts .row:before {
  content: '';
  display: block;
  clear: both;
  height: 0;
}

#posts .post-item {
  margin: 0 0 2em 0;
  border: 0.1em solid #a2a2a2;
  border-radius: 0.5em;
}

#posts .thumb {
  display: inline-block;
  margin: 0 0 2em 0;
  width: 26.4em;
  height: 15.5em;
  text-align: center;
  border-radius: 0.35em;
  border: 0;
  position: relative;
  overflow: hidden;
}

#posts .thumb::before {
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
  border-radius: 0.35em;
  background: #222;
  content: '';
  height: 100%;
  left: 0;
  opacity: 0.3;
  position: absolute;
  z-index: 9;
  top: 0;
  width: 100%;
  display: block;
}

#posts .thumb:hover::before {
  opacity: 0.6;
}

#posts .thumb::after {
  -webkit-transition: opacity 0.2s ease-in-out;
  transition: opacity 0.2s ease-in-out;
  border-radius: 0.35em;
  border: solid 3px rgba(255, 255, 255, 0.5);
  color: #fff;
  content: 'View';
  display: inline-block;
  font-size: 0.8em;
  font-weight: 400;
  left: 50%;
  line-height: 2.25em;
  margin: -1.25em 0 0 -3em;
  opacity: 0;
  padding: 0 1.5em;
  position: absolute;
  z-index: 10;
  text-align: center;
  text-decoration: none;
  top: 50%;
  white-space: nowrap;
}

#posts .thumb:hover::after {
  opacity: 1;
}

#posts .thumb img {
  display: block;
  border-radius: 0.35em;
  width: 100%;
  margin-top: 7.75em;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
}

#posts .post-item .tag-bar {
  margin: 0em;
}

#posts .post-item .tag-bar a {
  display: inline-block;
}

#posts .post-item .tag-bar p {
  font-size: 0.8em;
  line-height: 1.5em;
  margin-right: 0.4em;
  padding: 0.1em 0.4em;
  border-radius: 3px;
  color: #a2a2a2;
  background-color: #eee;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

#posts .post-item .tag-bar p:hover {
  color: #feffff;
  background: #40a9ff;
}

#posts .post-item .post-title {
  font-size: 1.6em;
  line-height: 1.6em;
  margin: 0 0 0.5em 0;
  color: #787878;
  -webkit-transition: color 0.2s;
  transition: color 0.2s;
  /* max-width: 22em; */
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

#posts .post-item .post-title:hover {
  color: #40a9ff;
}

#posts .post-item p.time {
  font-size: 0.8em;
  line-height: 1.5em;
  color: #a2a2a2;
  margin: 0;
}

.header-nav {
  position: fixed;
  z-index: 99;
  top: 0;
  right: 0;
  width: var(--content-width);
  padding: 0.4em 4em 0 4em;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f5f5f5f5;
  border-bottom: 1px solid #ddd;
  font-size: var(--main-font-size);
}

.header-nav-item {
  font-size: 1.2em;
  line-height: 3.2em;
  color: #333 !important;
  text-decoration: none !important;
  padding: 0 1.2em;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
  border: 1px solid transparent;
  border-bottom: none;
}

@media (max-width: 1280px) {
  .header-nav-item {
    font-size: 1em;
    line-height: 3em;
    padding: 0 1em;
  }
}

.header-nav-item.active,
.header-nav-item:hover {
  background-color: #fff;
  border-color: #ddd;
  position: relative;
}

.ant-dropdown-link.active {
  color: #40a9ff;
}

.header-nav-item.active::after,
.header-nav-item:hover::after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -1px;
  width: 100%;
  height: 1px;
  background: #fff;
}

